// src/components/Header.js
import React from 'react';
import { Navbar, Nav, Container, Col, Row } from 'react-bootstrap';
import './components.css';
import memoji from '../assets/images/memo.png';
import { Bounce, Zoom } from "react-awesome-reveal";
import down from "../assets/images/down.gif";

const Home = () => {
  return (
    <Container fluid className='lexend back'>
        
        <Row className='mt-sm-0 pt-sm-0' style={{height:'100vh'}}>
           
            <Col xs={{order:2}} className='ms-5 d-flex flex-column justify-content-md-center order-sm-2 order-md-2 mt-sm-5 pt-sm-0' >
            <Zoom>
                <h1 className='white'>Julian Cruz </h1>
                <h1 className='white'>Desarrollador Full Stack</h1>
                <span className='white'>
                +6 aos de experiencia<br/>
Solida experiencia en la creacin de aplicaciones mobiles y web dnamicas y escalables.
                </span>
            </Zoom>

            </Col>
            <Col className='d-flex flex-column mt-5 justify-content-center align-items-center order-sm-1 order-xs-1'>
            <Bounce cascade>
            <img  className='text-center ' width={300} src={memoji} alt="JulianMemoji" />
            </Bounce>
            </Col>
        </Row>
    </Container>
  );
};

export default Home;